.noteContainer {
    position: relative;
}

.backBtn {
    position: absolute;
    z-index: 1;
    top: 15px;
    left: 15px;
    border: 1px solid rgb(26, 25, 25);
    background-color: #fafafa;
    padding: 5px 30px;
    border-radius: 5px;
    font-family: 'Poppins', sans-serif;
