}

.backBtn:hover {
    background-color: #8bc34a;
    color: white;
}

/* NEW NOTES */

.noteTitle{ 
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    font-family: 'Dela Gothic One', cursive;
    letter-spacing: 2px;
    height: 22vh;
    top: 38px;
    /* DOUBLE CHECK ON HEIGHT FOR XL-SCREEN WAS 17VH */
}

.noteTitle::after {
    content: " ";
    display: block;
    position: absolute;
    height: 3px;
    min-width: 21%;
    background-color: #8bc34a;
    top: 57px;
}

.newNote {
    position: relative;
    height: 100vh;
