    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;

}

.col-12 {
    position: absolute;
    top: 104px;
    left: 44%;
}

.noteFormContainer {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.titleLabel {
    font-family: 'Poppins', sans-serif;
