/* LIST OF NOTES */

.listOfNotes {
    position: relative;
    height: 100vh;
    background-color: rgb(255, 255, 255);
    left: 0;
    bottom: 0;
}

.myNotesTitle {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-family: 'Dela Gothic One', cursive;
    letter-spacing: 2px;
    position: relative;
    height: 10vh;
}

.myNotesTitle::after {
    content: " ";
    display: block;
    position: absolute;
    height: 3px;
    min-width: 21%;
    background-color: #8bc34a;
    bottom: 1px;
}


.liContainer {
    position: absolute;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 65%;
    height: 59%;
}

.list-group-item {
    cursor: pointer;
}


li:nth-child(odd) {
    background-color: #f5f5f5;
  }



